import React from 'react';
import styles from './styles/login-style.module.css';
import logo from '../images/bohiologo.png';

export const Login = (props) => {
  return (
    <div className={styles.loginContainer}>
      <div className="logoContainer">
        <img alt="logo" src={logo}/>
      </div>
      <div className={styles.loginForm}>
        <form>
          <input type="text" placeholder="username or email"></input>
          <input type="password" placeholder="password"></input>
          <div className={styles.recovery}><a>Forgot Password?</a></div>
          <div><input type="checkbox"></input><span>Remember Me</span></div>
          <input
            onClick={(e) => props.onSignIn(e)}
            type="button"
            value="Sign In"></input>
        </form>
      </div>
    </div>
  );
}
