import React from 'react';
import ReactDOM from 'react-dom';

export const Slider = (props) => {

  const defaultBackgroundColor = '#040f0f';
  const defaultWidth = '300px';
  const pxToNumber = (px) => {
    return(Number(px.replace('px', '')));
  };

  const setBorderWidth = (width) => {
    return ((1/300)*width);
  }

  const setBorderRadius = (width) => {
    return ((5/300)*width);
  }

  const setSliderHeight = (width) => {
    return ((1/30)*width);
  }

  const setSliderRadius = (width) => {
    return ((1/5)*width);
  }

  let contStyle =  {
    display: 'flex',
    flexDirection : 'column',
    alignItems : 'center',
    justifyContent : 'center',
    width : defaultWidth,
//    borderWidth : '1px',
//    borderStyle : 'solid',
//    borderColor : 'grey',
//    borderRadius : '5px',
    padding : '2%',
    backgroundColor : defaultBackgroundColor
  }

  let sliderCont =  {
    display: 'flex',
    flexDirection : 'row',
    width: 'inherit',
    alignItems : 'center'
  }

  let sliderStyle = {
    outline : '0',
    webkitAppearance: 'none',
    display: 'flex',
    flexDirection : 'row',
    height : '8px',
    backgroundColor : '#4feb34',
    borderRadius : '5px',
    borderWidth : '7px',
    borderColor : '',
    width : 'inherit'
  }

  if (props.backgroundColor){contStyle.backgroundColor = props.backgroundColor}
  if (props.sliderColor){sliderStyle.backgroundColor = props.sliderColor}
  if (props.setSliderHeight){sliderStyle.height = props.setSliderHeight}
  if (props.width){
    contStyle.width = props.width
    contStyle.borderWidth = String(setBorderWidth(pxToNumber(props.width))) + 'px';
    contStyle.borderRadius = String(setBorderRadius(pxToNumber(props.width))) + 'px';
  }

  return (
    <div style={contStyle}>
      <div style={sliderCont}>
        {/*<span>{'$' + props.min}</span>*/}
        <div style={{
            fontWeight:'bold',
            marginRight:'2%',
            marginLeft:'2%',
            color:'#4feb34'}}><span>{'$' + props.val}</span></div>
        <input style={sliderStyle}
          type="range"
          min={props.min}
          max={props.max}
          value={props.val}
          onChange={props.changeHandler}
          className="slider" />
        {/*<span>{'$' + props.max}</span>*/}
      </div>
    </div>
  );
}

export class Sandbox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value : 30
    }
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange (event) {
    this.setState({value : event.target.value});
  }

  render() {
    return(
      <div>
        <Slider
          width="300px"
          min="1"
          max="100"
          val={this.state.value}
          changeHandler={this.handleChange}/>
      </div>
    );
  }
}
