import React from 'react';
import ReactDOM from 'react-dom';
import {OrderRow} from './order-row';
import {theme} from './main';

export class Orders extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    //get user orders
    fetch("./downstream-orders/")
    .then((res) => res.json())
    .then((data) => {/*render order list*/});
  }

  render () {
    let headers = {
      width : '100%',
      backgroundColor : theme.black,
      color : 'white',
      height : '50px',
      display : 'flex',
      justifyContent : 'center',
      alignItems : 'center',
      fontWeight : 'bold'
    }

    let ordersStyle = {
      backgroundColor : 'gray'
    }

    let data = [
      [1, '01.01.19', 'Agroproduce'],
      [2, '01.01.19', 'Agroproduce'],
      [3, '01.01.19', 'Agroproduce'],
      [4, '01.01.19', 'Agroproduce'],
      [5, '01.01.19', 'Agroproduce'],
      [6, '01.01.19', 'Agroproduce'],
      [7, '01.01.19', 'Agroproduce'],
      [8, '01.01.19', 'Agroproduce'],
    ]//this.props.orders
    const orders = data.map((info) => {
      const information = {
        price : '5',
        address : '0001',
        payment : '000002',
        buyer : 'estebi',
        id : info[0]
      }
      return (
        <div style={{margin : '10px'}}>
        <OrderRow
          orderDate={info[1]}
          supplier={info[2]}
          info={information}
          />
      </div>
      );
    });
    return (
      <div className="Orders">
        <div className="InProcess" style={ordersStyle}>
          <div style={headers}><span>In Process</span></div>
          {orders}
        </div>
        <div className="Processed"  style={ordersStyle}>
          <div style={headers}><span>Processed</span></div>
          {orders}
        </div>
      </div>
    );
  }
}
