import React from 'react';
import search from '../images/search.png'

export const InputField = (props) => {
  const width = '200px';
  const height = '50px';

  const pxToNumber = (px) => {
    return(Number(px.replace('px', '')));
  };

  let contStyle = {
    width : 'auto',
    height : 'auto',
    display : 'flex',
    flexDirection : 'row',
    alignItems : 'center',
    justifyContent : 'flex-start',
  }

  let text = {
    paddingTop : '1px',
    paddingBottom : '1px',
    //position : 'absolute',
    borderTopLeftRadius : '5px',
    borderBottomLeftRadius : '5px',
    height : 'inherit',
    width : 'auto',
    backgroundColor : 'blue',
    display : 'flex',
    flexDirection : 'column',
    justifyContent : 'center',
    paddingLeft : '2px',
    paddingRight : '2px',
    alignItems : 'center',
    fontWeight : 'bold',
    color : 'white',
    fontSize : '70%'
  }

  let inputContStyle = {
    height : 'inherit',
    display : 'flex',
    flexDirection : 'row',
    alignItems : 'center',
  }

  let inputStyle = {
    width : '60',
    height:'inherit',
    margin : '0px',
    paddingLeft : '10px',
    paddingTop : '0px',
    paddingBottom : '0px',
    fontWeight : 'bold',
    display : 'flex',
    flexDirection : 'row',
    justifyContent : 'center',
    backgroundColor : 'white',
    borderTopRightRadius : '5px',
    borderBottomRightRadius : '5px',
    border : '1px solid blue'
  }

  if (props.height) {contStyle.height = props.height}
  if (props.width) {inputStyle.width = props.width}
  if (props.fontSize) {text.fontSize = props.fontSize}


  return (
    <div style={contStyle}>
      <div style={inputContStyle}>
        <span style={text} id="text">
          {props.text}
        </span>
        <input style={inputStyle}/>
      </div>
    </div>
  );
}
