import React from 'react';
import ReactDOM from 'react-dom';
import {Tile} from './tile';
import {Slider} from './slider';
import {Checkbox} from './checkbox';
import {SearchTools} from './search-tools';
import logo from '../images/cp1.png';
import {theme} from './main'
import styles from './styles/catalog.module.css';

export const Catalog = (props) => {

  let data = props.catalogData;
  let catalog = data.map((data, index) => {
    return (
      <Tile
        id={index}
        onClick={(event) => props.onSupplierClick(event)}
        onAdd={(e) => props.onAdd(e)}
        type={props.catalogType}
        price={data.price}
        unit={data.unit}
        title={data.title}
        supplier={data.supplier}
        image={data.image}
        available={data.stock}
        width='200px'
        textColor="white"
        />
    );
  });

  let catalogCont = {
    width: '100%',
    height: '100%',
    display : 'flex',
    flexDirection : 'column',
    alignItems : 'center',
    justifyContent : 'center'
  }

  let catalogRow  = {
    width : '100%',
    display : 'flex',
    flexDirection : 'row',
  }

  return (
    <div>
      <SearchTools
        onFilterChange={(e) => props.onSliderChange(e)}
        productFilter={props.productFilter}/>
      <div style={catalogCont}>
        <div className={styles.resInfo}>
          {"Showing 15 results out of 130"}
        </div>
        <div className={styles.catalog}>
          {catalog}
        </div>
      </div>
    </div>
  );
}
