import React from 'react';
import ReactDOM from 'react-dom';
import {Button} from './button';
import chinaGuineo from '../images/chinaGuineo.jpg';
import {theme} from './main';

export const Tile = (props)  => {
  let width = '300px';
  let buttonSize = 'medium';
  let titleSize = '100%';
  const setDim = (d) => {
    return(String(d*(45/40)) + 'px');
  };

  const pxToNumber = (px) => {
    return(Number(px.replace('px', '')));
  };

  const setRadius = (r) => {
    return(String(r*(1/40)) + 'px');
  };

  const qtyWidth = (r) => {
    return(String(r*(8/40)) + 'px');
  };

  const qtyHeight = (r) => {
    return(String(r*(25/400)) + 'px');
  };

  const imageHeight = (r) => {
    return(String(r*(202.5/400)) + 'px');
  };

  const color = '#4feb34';
  const bgColor1 = '#073b3a';
  const bgColor = 'lightgrey';
  let contStyle = {
    position : 'relative',
    height: '450px',
    width: width,
    display: 'flex',
    flexDirection : 'column',
    justifyContent : 'center',
    alignItems : 'center',
    backgroundColor : bgColor,
    //border : '1px solid black',
    padding: '0.5%',
    borderRadius : '10px',
    margin: '5px',
    marginTop : '30px',
    marginbottom : '30px',
    cursor : 'pointer'
  }

  const imgStyle = {
    height : '100%',
    width : '100%',
    objectFit: 'contain'
  }

  const imgContStyle = {
    width: '100%',
    height: '202.5px',
    display : 'flex',
    borderRadius: '2px 2px 0px 0px',
    flexDirection : 'column',
    justifyContent : 'center',
    backgroundColor: 'white',
  }

  const midStyle = {
    display : 'flex',
    flexDirection : 'column',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    width: '100%',
  }

  const qtyStyle = {
    textAlign: 'center',
    backgroundColor : 'black',
    width: '80px',
    color: 'white',
    border : 'none',
    height: '25px',
    borderRadius: '5px',
    borderWidth: '3px',
    borderColor : theme.blue,
    borderStyle : 'solid',
    fontWeight : 'bold',
    padding: '3px',
    fontSize : '80%',
    margin: '0px'
  }

  const titleStyle = {
    color: theme.black,
    fontSize: titleSize,
    fontWeight: 'bold'
  }

  const txtStyle = {
    color : theme.black,
    fontSize : '100%',
    fontWeight: 'bold',
  }

  let quantityStyle = {
    color: theme.black,
    display:'flex',
    flexDirection : 'column',
    alignItems : 'center'
  }

  let buttonStyle = {
    //padding:'5%',
    margin:'5px'
  }

  let clickArea = {
    width : '100%',
    height : '100%',
    position : 'absolute',
    zIndex : '2',
    display : 'none'
  }

  let checkoutTotalStyle = {
    display : 'none'
  }

  if (props.tileColor) {contStyle.backgroundColor = props.tileColor}

  if (props.width) {
    contStyle.width = props.width;
    contStyle.height = 'auto';//setDim(pxToNumber(props.width));
    contStyle.borderRadius = setRadius(pxToNumber(props.width));
    qtyStyle.width = qtyWidth(pxToNumber(props.width));
    qtyStyle.height = qtyHeight(pxToNumber(props.width));
    imgContStyle.height = imageHeight(pxToNumber(props.width));
  }

  if (props.type === "supplier") {
    quantityStyle.display = 'none';
    buttonStyle.display = 'none';
    txtStyle.display = 'none';
    imgContStyle.height = '75%';
    clickArea.display = 'flex';
  }

  if (props.type === 'product') {
    contStyle.cursor = "";
    buttonStyle.padding = '0px';
  }

  if (props.type === "checkout") {
    checkoutTotalStyle.display = "flex";
    quantityStyle.display = 'none';
    buttonStyle.padding = '0px';
    buttonSize = "xsmall";
  }


  if (pxToNumber(contStyle.width) < 400){
    buttonSize = 'xsmall';
    txtStyle.fontSize = '75%';
    titleStyle.fontSize = '100%';
    contStyle.justifyContent = 'flex-start';
  }
  if (pxToNumber(contStyle.width) < 300){buttonSize = 'xsmall'}

  const productText = <i>{'In Stock(' + props.available + ')'}</i>;
  const checkoutText = <i>{'Order Qty: ' + props.orderQty}</i>

return(
  <div
    style={contStyle}
    id={props.id}
    >
    <div
      style={clickArea}
      onClick={(event) => props.onClick(event)}
      id={props.title}
      class={props.type}></div>
    <div className="image" style={imgContStyle}>
      <img style={imgStyle} src={props.image} />
    </div>
    <div className="midSection" style={midStyle}>
        <span style={titleStyle}>{props.title}</span>
        <span style={titleStyle}>{props.supplier}</span>
        <span style={txtStyle}>{'$' + props.price + '/' + props.unit}</span>
        <span style={txtStyle}>
          {props.type === 'checkout' ? checkoutText : productText}
        </span>
        <div className="checkoutTotal" style={checkoutTotalStyle}>
          <span>{"Total: $" + String(props.total) }</span>
        </div>
    </div>
    <div className="quantityPicker" style={quantityStyle}>
      <span style={txtStyle}>Qty:</span><input style={qtyStyle}
      type="text"
      name="qty"/>
    </div>
    <div className="addToCart" style={buttonStyle}>
      <Button id={props.id}
        color={theme.blue}
        text={props.type === 'product' ? '+' : '-'}
        onClick={(e) => props.onAdd(e)}
        size="small"
        type="add"
        />
    </div>
  </div>
);
}
