import React from 'react';
import ReactDOM from 'react-dom';

export const RowButton = (props) => {

  let buttonStyle = {
    webkitAppearance : 'none',
    appearance : 'none',
    backgroundColor : 'green',
    borderShadow : 'none',
    borderColor : 'green',
    borderRadius : '5px',
    color : 'white',
    height : 'inherit',
    width : 'inherit',
    marginRight : '2px'
  }

  let buttonRowStyle = {
    width : '100px',
    height : '30px',
    display : 'flex',
    flexDirection : 'row',
    justifyContent: 'center',
    alignItems : 'center'
  }

  if (props.selected) {buttonStyle.backgroundColor = 'blue'}
  if (props.height) {buttonRowStyle.height = props.height}
  if (props.width) {buttonRowStyle.width = props.width}

  return(
    <div className="buttonRow" style={buttonRowStyle}>
      <button onClick={(e) => props.handleModify(e)}
        style={buttonStyle}>MODIFY</button>
      <button onClick={(e) => props.handleCancel(e)}
        style={buttonStyle}>CANCEL</button>
    </div>
  );
}
