import React from 'react';
import ReactDOM from 'react-dom';
import {Searchbar} from './search-bar';
import {Slider} from './slider';
import {theme} from './main';
import {Checkbox}  from './checkbox';
import {InputField} from './input-field';

export class Filter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value : 30
    }
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange (event) {
    this.setState({value : event.target.value});
  }

  render () {
    return (
      <div className="productFilter">
        <div className="scalingBar">
          <Slider
            width="300px"
            min="1"
            max="100"
            val={this.state.value}
            changeHandler={this.handleChange}/>
        </div>
        <div className="productCategories">
          <Checkbox
            onChange={(e) => this.props.onFilterChange(e)}
            value="Frutas"/>
          <Checkbox
            onChange={(e) => this.props.onFilterChange(e)}
            value="Vegetales"/>
        </div>
      </div>
    );
  }
}

export class SearchTools extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filterIsShowing : false,
    }
    this.handleFilter = this.handleFilter.bind(this);
  }

  handleFilter() {
    this.setState({filterIsShowing : !this.state.filterIsShowing});
  }

  render() {
    let searchToolsStyle = {
      width:'100%',
      display : 'flex',
      flexDirection : 'row',
      justifyContent : 'center',
      alignItems : 'center'
    }

    let filterStyle = {
      width : '76.9%',
      display : this.state.filterIsShowing ? 'flex' : 'none',
      flexDirection : 'row',
      justifyContent : 'flex-start',
      alignItems : 'center',
      marginBottom : '20px',
    }

    let searchArea = {
      display : 'flex',
      flexDirection : 'column',
      justifyContent : 'center',
      alignItems : 'center',
      backgroundColor : theme.black
    }

    const supplierFilter = (
      <Checkbox
      onChange={(e) => this.props.onFilterChange(e)}
      value="Tracking Enabled"/>
  );
  const productFilter = <Filter/>;
  return (
    <div style={searchArea}>
      <div style={searchToolsStyle}>
        <div style={{
            marginTop:'20px',
            marginBottom:'20px',
            marginLeft : '20px',
          }}>
          <Searchbar
            onChange={(e) => this.props.onFilterChange(e)}
            width="80%"
            height="50px"/>
        </div>
        <div style={{marginRight : '20px'}}>
          <button
            onClick={() => this.handleFilter()}>
            FILTER
          </button>
        </div>
      </div>
      <div style={filterStyle}>
        {
          this.props.productFilter ?
          productFilter : supplierFilter
        }
      </div>
    </div>
  );
}
}
