import React from 'react';
import ReactDOM from 'react-dom';
import {InputField} from './input-field.js';

export class Settings extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      category : 'Profile'
    }
    this.handleClick = this.handleClick.bind(this);
    this.handleSaveChanges = this.handleSaveChanges.bind(this)
  }

  componentDidMount() {
    fetch("./user-settings/")
    .then((res) => res.json())
    .then((data) => {/*render with settings data*/});
  }

  handleClick(e) {
    this.setState({category : e.target.innerHTML});
  }

  handleSaveChanges() {
    const changes = {
      "companyName" : "name val",
      "username" : "username val",
      "email" : "email val",
      "number" : "number val",
      "shippingAddress" : "address val",
    }
    const init = {
      "method" : "POST",
      "body" : JSON.stringify(changes)
    }
    fetch("./settings-changes/")
    .then((res) => res.json())
    .then((data) => {/*render with new settings data*/});
  }

  render () {

    const category = (this.state.category === 'Profile')
    const height = '50px';

    let settingsContStyle = {
      display: 'flex',
      flexDirection: 'row',
      justifyContent : 'center',
      alignItems : 'center',
      width : '100%',
      height : '100%'
    }

    let settingsNavStyle = {
      width: '20%',
      height: '80%',
      display: 'flex',
      flexDirection: 'column',
      justifyContent : 'flex-start',
      alignItems : 'center',
      padding : '10px',
      backgroundColor : 'lightgrey'
    }

    let categoryStyle = {
      display: 'flex',
      flexDirection: 'row',
      justifyContent : 'center',
      alignItems : 'center',
      height : '50px',
      width : '100%',
      border : '3px solid blue',
      marginBottom : '5px',
      cursor: 'pointer',
    }

    let settingsFieldsStyle = {
      width: '60%',
      height: '80%',
      backgroundColor : 'grey',
      display: 'flex',
      flexDirection: 'column',
      justifyContent : 'flex-start',
      alignItems : 'flex-start',
      padding : '10px',
    }

    let pfieldsStyle = {
      display: category ? 'flex' : 'none',
      flexDirection: 'column',
      justifyContent : 'space-between',
      alignItems : 'flex-start',
      height : 'inherit'
    }

    let paymentFieldStyle = {
      display : !category ? 'flex' : 'none',
      flexDirection: 'column',
      justifyContent : 'space-between',
      alignItems : 'flex-start',
      height : 'inherit',
      marginBottom : '20px'
    }

    return (
      <div className="settingsCont" style={settingsContStyle}>
        <div className="settingsNav" style={settingsNavStyle}>
          <div onClick={(e) => this.handleClick(e)}
            style={categoryStyle}>Profile</div>
          <div onClick={(e) => this.handleClick(e)}
            style={categoryStyle}>Payment</div>
          <button onClick={this.handleSaveChanges()}> Save Changes</button>
        </div>
        <div className="settingsFields" style={settingsFieldsStyle}>
          <div className="profileFields" style={pfieldsStyle}>
            <InputField height={height} text="Company Name"/>
            <InputField height={height} text="Username"/>
            <InputField height={height} text="Email"/>
            <InputField height={height} text="Number"/>
            <InputField height={height} text="Shipping Address"/>
            <button>Change email/password</button>
          </div>
          <div className="paymentFields" style={paymentFieldStyle}>
            <div style={paymentFieldStyle} className="bank">
              <h4>Bank</h4>
              <InputField height={height} text="Bank"/>
              <InputField height={height} text="Account No."/>
              <InputField height={height} text="Routing No."/>
            </div>
            <div style={paymentFieldStyle} className="card">
              <h4>Card</h4>
              <InputField height={height} text="Card No."/>
              <InputField height={height} text="Name on Card"/>
              <InputField height={height} text="Exp. Date"/>
              <InputField height={height} text="CCV"/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
