import React from 'react';
import ReactDOM from 'react-dom';
import {RowButton} from './rowbutton';
import {TrackingBar} from './tracking-bar';
import dropdown from '../images/dropdown.png';

export class OrderRow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showDropdown : false
    }
    this.handleDropdown = this.handleDropdown.bind(this);
  }

  handleDropdown() {
    this.setState({showDropdown : !this.state.showDropdown});
  }

  render () {

    let contStyle = {
      display : 'flex',
      flexDirection : 'column',
      width : '800px',
    }

    let orderRowStyle = {
      fontWeight : 'bold',
      display : 'flex',
      width : 'inherit',
      flexDirection : 'row',
      alignItems : 'center',
      justifyContent : 'space-between',
      padding : '10px',
      border : '1px solid grey',
      borderRadius : '5px',
      backgroundColor : 'white'
    }

    let infoAreaStyle = {
      display : 'flex',
      flexDirection : 'row',
      alignItems : 'center',
    }

    let dropdownAreaStyle = {
      display : this.state.showDropdown ? 'flex' : 'none',
      flexDirection : 'column',
      paddingTop : '10px',
      width : 'inherit',
      //border: '1px solid lightgrey'
      backgroundColor : 'beige'

    }

    let statusStyle = {
      display : 'flex',
      flexDirection : 'row',
      alignItems : 'center',
      justifyContent : 'space-between'
    }

    let infoField = {
      padding : '10px',
      display : 'flex',
      flexDirection : 'row',
      justifyContent : 'space-between'
    }

    let dropDownButtonStyle = {
      width : '30px',
      height : '30px',
      border : '1px solid grey',
      cursor : 'pointer',
      marginRight : '10px',
      display : 'flex',
      flexDirection : 'column',
      justifyContent : 'center',
      alignItems : 'center',
      borderRadius : '30px'
    }

    let dropdownImage = {
      objectFit : 'contain',
      height : '50%',
      width : '50%'
    }

    let even = {backgroundColor : 'lightgrey'}

    return (
      <div style={contStyle}>
        <div
          style={orderRowStyle}
          className="orderRow">
          <div
            style={infoAreaStyle}
            className="infoArea">
            <div
              style={dropDownButtonStyle}
              className="dropdownButton"
              onClick={() => this.handleDropdown()}>
              <img style={dropdownImage} src={dropdown}/>
            </div>
            <span>{this.props.orderDate}</span>
            <div className='separator'style={{
                paddingLeft : '10px',
                paddingRight : '10px',
              }}> | </div>
            <span>{this.props.supplier}</span>
          </div>
          <div style={{paddingRight : '20px'}} className="changes">
            <RowButton />
          </div>
        </div>
        <div className="dropdownArea" style={dropdownAreaStyle}>
          <div style={Object.assign({}, infoField, even)}>Total:
            <span style={{fontWeight : 'bold'}}>{'$'+this.props.info.price}</span></div>
          <div style={infoField}>To be deliverd at:
            <span style={{fontWeight : 'bold'}}>{this.props.info.address}</span></div>
          <div style={Object.assign({}, infoField, even)}>Payment Method:
            <span style={{fontWeight : 'bold'}}>{this.props.info.payment}</span></div>
          <div style={infoField}>Buyer:
            <span style={{fontWeight : 'bold'}}>{this.props.info.buyer}</span></div>
          <div style={Object.assign({}, infoField, even)}>Order ID: #
            <span style={{fontWeight : 'bold'}}>{this.props.info.id}</span></div>
          <div style={statusStyle}><span>Status: </span>
            <TrackingBar width="500px" stage='3'/>
        </div>
        <div style={{paddingTop : '20px', paddingBottom : '20px'}}>
          <button style={{
              width: '100px',
            }}>EXPORT</button>
        </div>
      </div>
    </div>
  );
}
}

