import React from 'react';
import ReactDOM from 'react-dom';

export const Button = (props)  => {
  let height = '50px';
  let scaleFactor = 1;

  const setWidth = (h) => {
    return(String(h*1.6180339887498948482*2) + 'px');
  };

  const pxToNumber = (px) => {
    return(Number(px.replace('px', '')));
  };

  let style = {
    backgroundColor : 'rgba(0,0,0,0)',
    border : '3px solid ' + props.color,
    borderRadius : '30px',
    color : props.color,
    height : height,
    width : setWidth(pxToNumber(height)),
    fontSize : '80%',
    fontWeight : 'bold',
    cursor : 'pointer'
  };

  if (props.size === 'xsmall') { scaleFactor = 0.5;}

  if (props.size === 'small') { scaleFactor = 0.75;}

  if (props.size === 'medium') {}

  if (props.size === 'large') {scaleFactor = 1.25;}

  if (props.size === 'xlarge') {scaleFactor = 1.5;}

  if (props.size === 'xxlarge') {scaleFactor = 1.75;}

  if (props.size === 'xxxlarge') {scaleFactor = 2;}

  let scaled = scaleFactor*(pxToNumber(height));
  let radius = String(0.6*scaled) + 'px';
  style.height = String(scaled) + 'px';
  style.width = setWidth(pxToNumber(String(scaled) + 'px'));
  style.fontSize = String(2*scaled) + '%';
  style.borderRadius = radius;

  if (props.type === 'add') {
    style.fontSize = "100%";
    style.width = "30px";
    style.height = "30px";
    style.borderRadius = "5px";
  }

  return(
    <button
      id={props.id}
      style={style}
      onClick={(event) => props.onClick(event)}
      onHover>
      {props.text}
    </button>
  );
}
