import React from 'react';
import ReactDOM from 'react-dom';
import {Tile} from './tile';
import image from '../images/chinaGuineo.jpg';
import {theme} from './main';


export const ChangeableInput = (props) => {
  return (
    <div className="container">
      <div className="current">
      </div>
      <div className="modify">
      </div>
    </div>
  );
}

const PaymentForm = (props) => {
  return(
    <div className="paymentFormCont">
      <div>

      </div>
    </div>
  );
}

export class Checkout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      subView : 'products'
    }
    this.handlePaymentClick = this.handlePaymentClick.bind(this);
  }

  handlePaymentClick() {
    this.setState({subView : 'checkout'})
  }

  render () {
    let checkoutStyle = {
      display : 'flex',
      flexDirection : 'column',
      justifyContent : 'space-between',
      overflowY : 'none',
      height : '100%',
      width : '100%',
    }

    let productsStyle = {
      display : 'flex',
      flexDirection : 'row',
      flexWrap : 'wrap',
      overflowY : 'scroll'
    }

    let functionsStyle = {
      height : '50%',
      backgroundColor : 'gray',
      display : 'flex',
      flexDirection : 'row'
    }

    let button = {
      margin : '10px'
    }

    let data = this.props.productsChosen;
    let orderTotal = 0;
    let tiles = data.map((info, index) => {
      let total = info.price * info.quantity;
      orderTotal = orderTotal + total;
      return (
        <Tile
          id={index}
          addHandler={(e) => this.props.onRemove(e)}
          type="checkout"
          title={info.title}
          image={image}
          textColor='white'
          total={total}
          price={info.price}
          unit="each"
          width="200px"
          orderQty={info.quantity}
          />
      );
    })
    const checkoutProducts = {
      content : tiles,
      functions : (
        <div>
          {/*<div style={button}>
            <button onClick={}>ADD MORE PRODUCTS</button>
          </div>*/}
          <div style={button}>
            <button onClick={() => this.handlePaymentClick()}>
              PROCEED TO SAVE
            </button>
          </div>
          <div><span>{"ORDER TOTAL: $" + String(orderTotal)}</span></div>
        </div>
      )
    }
    const checkoutPayment = {
      content : <PaymentForm/>,
      functions : (
        <div>
          <div style={button}>
            <button onClick={() => this.props.onSubmitOrder()}>
              SAVE ORDER
            </button>
          </div>
        </div>
      )
    }
    const subview = this.state.subView === 'products';
    return (
      <div className='Checkout' style={checkoutStyle}>
        <div className='Products' style={productsStyle}>
          {subview ? checkoutProducts.content : checkoutPayment.content}
        </div>
        <div className='Functions' style={functionsStyle}>
          {subview ? checkoutProducts.functions : checkoutPayment.functions}
        </div>
      </div>
    );
  }
}
