import React from 'react';
import {Tile} from './tile';
import {Slider} from './slider'

export const TrackingBar = (props) => {

  let cont = {
    display : 'flex',
    flexDirection : 'column',
    alignItems : 'center',
    justifyContent : 'flex-start',
    padding : '10px',
    width : '400px',
    position : 'relative'
  }

  let barStyle = {
    width : '350px',
    height : '2px',
    borderStyle : 'solid',
    marginTop : '10.5px',
    borderWidth : '2px',
    borderRadius : '5px',
    borderColor : '#4feb34'
  }

  let progress = {
    height : 'inherit',
    width : '0%',
    backgroundColor : 'green'
  }

  let stagesStyle = {
    position : 'absolute',
    zIndex : '2',
    width : 'inherit',
    display : 'flex',
    flexDirection : 'row',
    justifyContent : 'space-between',
    alignItems : 'center'
  }

  let stage = {
    display : 'flex',
    flexDirection : 'column',
    justifyContent : 'center',
    alignItems : 'center'
  }

  let stage1 = {backgroundColor : 'white'}
  let stage2 = {backgroundColor : 'white'}
  let stage3 = {backgroundColor : 'white'}
  let stage4 = {backgroundColor : 'white'}

  let dot = {
    width : '20px',
    height : '20px',
    borderRadius : '15px',
    borderStyle : 'solid',
    borderColor : '#4feb34',
  }

  let bar = {
    height: '10px',
    width: '2px',
    backgroundColor: 'black',
    marginTop : '3px',
    marginBottom : '3px'
  }

  let stagesText = {
    width: 'inherit',
    marginTop : '5px',
    display : 'flex',
    flexDirection : 'row',
    justifyContent : 'space-between',
    alignItems : 'center',
  }

  let text = {
    display : 'flex',
    flexDirection : 'column',
    alignItems : 'center',
    fontWeight : 'bold',
    fontSize : '11px'
  }

  if (props.stage === '1') {
    progress.width = '0%';
    stage1.backgroundColor = 'green';
  }

  if (props.stage === '2') {
    progress.width = '33%';
    stage1.backgroundColor = 'green';
    stage2.backgroundColor = 'green';
  }

  if (props.stage === '3') {
    progress.width = '63%';
    stage1.backgroundColor = 'green';
    stage2.backgroundColor = 'green';
    stage3.backgroundColor = 'green';

  }

  if (props.stage === '4') {
    progress.width = '100%';
    stage1.backgroundColor = 'green';
    stage2.backgroundColor = 'green';
    stage3.backgroundColor = 'green';
    stage4.backgroundColor = 'green';
  }


  return (
    <div style={cont}>
      <div className="bar" style={barStyle}>
        <div classNAme='progress' style={progress}>
        </div>
      </div>
      <div className="stages" style={stagesStyle}>
        <div style={stage}>
          <div style={Object.assign({}, dot, stage1)}></div>
          <div style={bar}></div>
          <div style={text}>
            <span>sent</span>
            <span style={{color:'white'}}>deliverance</span>
          </div>
        </div>
        <div style={stage}>
          <div style={Object.assign({}, dot, stage2)}></div>
          <div style={bar}></div>
          <div style={text}>
            <span>processing</span>
            <span style={{color:'white'}}>in</span>
          </div>
        </div>
        <div style={stage}>
          <div style={Object.assign({}, dot, stage3)}></div>
          <div style={bar}></div>
          <div style={text}>
            <span>transporting</span>
            <span style={{color:'white'}}>in</span>
          </div>
        </div>
        <div style={stage}>
          <div style={Object.assign({}, dot, stage4)}></div>
          <div style={bar}></div>
          <div style={text}>
            <span>delivered</span>
            <span style={{color:'white'}}>deliverance</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export class Sandbox extends React.Component {
  render() {
    return (
        <div style={{
            display : 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
            height: '900px'
          }}>
          <TrackingBar stage="1"/>
          <TrackingBar stage="2"/>
          <TrackingBar stage="3"/>
          <TrackingBar stage="4"/>
          <Tile />
          <Slider min={1} max={100} val={30} />
        </div>
    );
  }
}
