import React from 'react';
import search from '../images/search.png'

export const Searchbar = (props) => {
  const width = '200px';
  const height = '50px';

  const pxToNumber = (px) => {
    return(Number(px.replace('px', '')));
  };

  let contStyle = {
    position : 'relative',
    width : width,
    height : height,
    display : 'flex',
    flexDirection : 'row',
    alignItems : 'center',
    justifyContent : 'flex-start'
  }

  let searchIcon = {
    position : 'absolute',
    height : '30px',
    width : '30px',
    paddingLeft : '10px',
    paddingRight : '10px',
    cursor : 'pointer',
    borderRight : '1px solid gray'
  }

  let searchPng = {
    width : '100%',
    height : '100%',
  }

  let inputContStyle = {
    width : 'inherit',
    height : 'inherit'
  }

  let inputStyle = {
    width:'inherit',
    height:'inherit',
    margin : '0px',
    paddingLeft : height,
    paddingTop : '0px',
    paddingBottom : '0px',
    fontWeight : 'bold',
    fontSize : '125%',
  }

  if (props.height) {
    contStyle.height = props.height;
    searchIcon.height = String((3/5)*pxToNumber(props.height)) + 'px';
    searchIcon.width = searchIcon.height;
    searchIcon.paddingLeft = String((1/5)*pxToNumber(props.height)) + 'px';
    searchIcon.paddingLeft = searchIcon.paddingRight;
    inputStyle.paddingLeft = props.height;
  }

  //remove  separator when search bar is sufficiently small or
  //when border props is false
  if (pxToNumber(props.height) < 30 || !props.separator) {
    searchIcon.borderRight = 'none'
  }

  if (props.width) {contStyle.width = props.width}

  return (
    <div style={contStyle}>
      <div style={searchIcon} onClick={() => props.handleClick()}>
        <img style={searchPng} src={search}/>
      </div>
      <div style={inputContStyle}>
        <input style={inputStyle}/>
      </div>
    </div>
  );
}
